import { Progress } from 'antd';
import { Clearfix } from 'components/lib/Clearfix';
import React from 'react';
import { styled } from 'styletron-react';
import PropTypes from 'prop-types';

const MenuTitle = styled('div', {
    float: 'left',
    fontWeight: 'bold',
    textTransform: 'uppercase',
});

const ProgressBackground = styled('div', {
    backgroundColor: 'rgba(255, 255, 255, .75)',
    paddingRight: '5px',
    paddingLeft: '5px',
    float: 'right',
});

export const ChecklistMenuTitle = ({ title, percentProgress }) => {
    let progress = null;

    if (percentProgress !== '0') {
        progress = (
            <ProgressBackground>
                <Progress type="circle" width={35} strokeWidth={8} percent={percentProgress} />
            </ProgressBackground>
        );
    }

    return (
        <>
            <MenuTitle>
                {title}
            </MenuTitle>
            {progress}
            <Clearfix />
        </>
    );
};

ChecklistMenuTitle.propTypes = {
    title: PropTypes.string.isRequired,
    percentProgress: PropTypes.number.isRequired,
};
